.premium-section {
  padding-top: 10rem;
  background: linear-gradient(to bottom, rgba(22, 22, 22, 0.3) 0%, rgba(22, 22, 22, 0.2) 75%, rgba(22, 22, 22, 0.8) 100%);

  p {
    margin-bottom: 5rem;
  }
}

.features-section {
  padding: 10rem 0;

  .featured-text {
    padding: 2rem;
  }

  .feature-text {
    padding: 3rem;
    font-size: 90%;
  }
}

@media (min-width: 992px) {
  .features-section .featured-text {
    padding: 0 0 0 2rem;
    border-left: 0.5rem solid #d2009c;
  }
}

@media (min-width: 992px) {
  .features-section .feature-text {
    padding: 5rem;

    hr {
      border-color: #d2009c;
      border-width: .25rem;
      width: 30%;
    }
  }
}

.lw-search-section {
  padding: 10rem 0;
  background: linear-gradient(to bottom, rgba(22, 22, 22, 0.1) 0%, rgba(22, 22, 22, 0.5) 75%, #161616 100%), url("../../imgs/home/people-2561578_1920.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: scroll;
  background-size: cover;

  input,
  select {
    box-shadow: 0 0.1875rem 0.1875rem 0 rgba(0, 0, 0, 0.1) !important;
    padding: 1.25rem 2rem;
    height: auto;
    font-family: 'Varela Round';
    font-size: 80%;
    text-transform: uppercase;
    letter-spacing: 0.15rem;
    border: 0;
  }

  .custom-select {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
  }
}

.card.lw-transparent-card {
  background-color: rgba(0, 0, 0, 0.6);
  border: 0;
  backdrop-filter: blur(3px) grayscale(0.4);
  padding-top: 16px;
}

.lw-feature-items {
  line-height: 2em;
}

.contact-section {
  padding: 5rem 0 0;

  .card {
    border: 0;
    border-bottom: 0.25rem solid #d2009c;

    h4 {
      font-size: 0.8rem;
      font-family: 'Varela Round';
      text-transform: uppercase;
      letter-spacing: 0.15rem;
    }

    hr {
      border-color: #d2009c;
      border-width: 0.25rem;
      width: 3rem;
    }
  }

  .social {
    margin-top: 5rem;

    a {
      text-align: center;
      height: 3rem;
      width: 3rem;
      background: rgba(255, 255, 255, 0.1);
      border-radius: 100%;
      line-height: 3rem;
      color: rgba(255, 255, 255, 0.3);

      &:hover {
        color: rgba(255, 255, 255, 0.5);
      }

      &:active {
        color: #fff;
      }
    }
  }
}

footer {
  padding: 5rem 0;
}

.lw-logo-img {
  height: 80px;
  max-width: 60vw;
}

@media (max-width: 768px) {

    .input-group > .form-control, 
    .input-group > .custom-select,
    input.form-control,
    select.form-control {
        width: 100%;
    }
  .lw-search-section {
    .input-group {
      display: block;
    }

    .input-group>.form-control,
    .input-group>.form-control-plaintext,
    .input-group>.custom-select,
    .input-group>.custom-file {
      color: #000000;
      margin-bottom: 6px;
      border-radius: 6px !important;
    }

    .btn {
      width: 100%;
      margin-bottom: 6px;
      border-radius: 6px !important;
    }
  }
}

// Light theme
// -----------------------------------------
html.lw-light-theme {

  body {
    color: #000000;
  }

  .masthead {
    background: linear-gradient(to bottom, rgba(255, 255, 255, 0.1) 0%, rgba(255, 255, 255, 0.2) 75%, #ffffff 100%), url("../../imgs/home/bg-masthead.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-attachment: scroll;
    background-size: cover;
    .text-white-50 {
      color: rgba(0,0,0, 0.5) !important;
    }
  }

  .bg-dark {
    background-color: #efefef !important;
    color: black;
  }

  footer.bg-dark {
    background-color: #fff !important;
  }

  footer.text-white-50 {
    color: rgba(50, 50, 50, 0.5) !important;
  }
  .features-section {
    .text-white-50 {
      color: rgba(50, 50, 50, 0.5) !important;
    }

    .text-white {
      color: #000000 !important;
    }
  }
}